import React from "react";
import { Radio } from "antd";

export default function VariableTypeSelector(props) {
  return (
    <Radio.Group
      {...props}
      buttonStyle="solid"
    >
      <Radio.Button value="simple">Simple</Radio.Button>
      <Radio.Button value="secret">Secret</Radio.Button>
    </Radio.Group>
  );
}
